@mixin make-badge {
  position: absolute;
  background: $boschLightBlue;
  color: $boschWhite;
  text-align: center;
  font-size: $font-size-tiny;
  font-weight: $font-weight-medium;
  border-radius: $gridSize;
  margin-left: -$gridSize;
  margin-top: -$gridSize;
  padding: 0 2px;
  line-height: $gridSize * 2;
  min-width: 12px;
  height: $gridSize * 2;
}

@mixin make-badge-hidden {
  display: none;
}

@mixin make-badge-warn {
  background: $boschSignalRed;
}

@mixin make-badge-accent {
  background: $boschSignalYellow;
}
