// supergraphic-line
//
// Renders the supergraphic-line, it will render a smaller
// image of the supergraphic by default. To render a bigger one,
// use the supergrapic-image mixin, explained below.
//
// Usage:
// <code>
// @include supergraphic-line($position: before, $height: $supergraphic-height, $width: 100%);
// </code>
//
// To add the supergraphic image for bigger viewports use the next mixin
//
// <code>
// @include supergraphic-image--sm($position: before);
// </code>
//
// Styleguide 0.4.7

@mixin supergraphic-line($position: before, $height: $supergraphic-height, $width: 100%) {
  /* stylelint-disable declaration-colon-space-after, declaration-block-trailing-semicolon */
  // After the version 7.3.0 the line before will be interpreted by stylelint as if it was
  // a css property, and would throw a false negative about the disabled rules
  &:#{$position} {
    /* stylelint-enable declaration-colon-space-after, declaration-block-trailing-semicolon */
    position: absolute;
    left: 0;
    top: 0;
    width: $width;
    height: $height;
    content: '';
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-image: url($supergraphic-path--xs);
    z-index: 100;
  }
}

@mixin supergraphic-image--sm($position: before) {

  /* stylelint-disable declaration-colon-space-after, declaration-block-trailing-semicolon */
  // After the version 7.3.0 the line before will be interpreted by stylelint as if it was
  // a css property, and would throw a false negative about the disabled rules
  &:#{$position} {
    /* stylelint-enable declaration-colon-space-after, declaration-block-trailing-semicolon */
    background-image: url($supergraphic-path--sm);
  }
}

//
// Images
// ==========================================================================

// doiuse-disable
// (doiuse is finding false positives in the base64 data, e.g. complaining about missing vw/vh support in IE on character sequence '8VH')

// Source: app/core/images/supergraphic_header__xs.svg
$supergraphic-path--xs: 'data:image/svg+xml;base64,';

