@mixin make-icon-pseudo-element($font-size: 24px) {
    font-family: "Bosch-Ic";
    font-style: normal;
    font-weight: normal;
    font-size: $font-size;

    vertical-align: middle;
}

@mixin make-icon($icon-name, $font-size: 24px) {
    font-family: "Bosch-Ic";
    font-style: normal;
    font-weight: normal;
    font-size: $font-size;

    content: $icon-name;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin make-icon-container($font-size: 24px) {
    line-height: 1; /* same as font-size for proper relative vertical positioning at font type median */
    vertical-align: 5%; /* align icon to median (not middle!) of Bosch Font (https://en.wikipedia.org/wiki/Mean_line) */

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &:before {
        @include make-icon-pseudo-element($font-size);
    }
}

@import "../bosch-ic";
@import "../bosch-ic-legacymapping";
