@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';

mat-dialog-container.mat-dialog-container {
  box-shadow: none;
  position: relative;
  border-radius: 0;
  padding: $gridSize*4;

  .dialog-header {
    margin: 34px 0 $gridSize*1.5 0;

    h1,h2,h3,h4 {
      padding: 0;
      margin: 0;
      @include make-font-regular($font-size-h3);
      line-height: $gridSize*4;
    }

    .close-button {
      position: absolute;
      top: 20px;
      right: 20px;
      line-height: 1;

      i {
        color: $boschDarkGray;
        transition: all .2s ease;
        margin: $gridSize*1.5;
        display: inline-block;

        &:hover {
          color: $boschBlack;
        }
      }
      .mat-button-wrapper :last-child:not(.mat-icon) {
        margin-right: 12px;
      }
    }
  }

  mat-dialog-content.mat-dialog-content,
  .dialog-content {
    margin: 0 0;
    padding: 0 0;
    @include make-font-regular(14px);
    line-height: 21px;
  }

  mat-dialog-actions.mat-dialog-actions,
  .dialog-actions {
    padding: $gridSize*4 0;
    margin-bottom: -$gridSize*4;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;

    .mat-button, .mat-raised-button {
      margin-left: $gridSize*2;
    }

    // hide actions if there is nothing inside
    &:empty {
      display: none;
    }
  }
}

@media all and (max-width: $screen-sm-min) {
  mat-dialog-container.mat-dialog-container {

    .dialog-header {
      margin: 26px 0 $gridSize*1.5 0;

      h1,h2,h3,h4 {
        @include make-font-regular($font-size-h4);
      }
    }

    mat-dialog-content.mat-dialog-content,
    .dialog-content {
      @include make-font-regular($font-size-base);
      line-height: 21px;
    }
  }
}

@media all and (max-width: $screen-xs-min) {
  mat-dialog-container.mat-dialog-container {
    padding: 14px;

    .dialog-header {
      margin: 28px 0 $gridSize*1.5 0;

      .close-button {
        top: 4px;
        right: 2px;
      }
    }

    mat-dialog-actions.mat-dialog-actions,
    .dialog-actions {
      margin-bottom: -14px;
      width: 100%;

      button {
        width: 100%;

        &.mat-button,
        &.mat-raised-button {
          margin-left: 0;

          &:not(:first-child) {
            margin-top: $gridSize*2;
          }
        }
      }
    }
  }
}
