@import '../mixins/icons';

.bosch-ic,
.Bosch-Ic {
  @include make-icon-container();
}

.bosch-ic-large,
.Bosch-Ic-Large {
  @include make-icon-container($icon-font-size-large);
}

.bosch-ic-medium,
.Bosch-Ic-Medium {
  @include make-icon-container($icon-font-size-medium);
}

.bosch-ic-normal,
.Bosch-Ic-Normal {
  @include make-icon-container($icon-font-size-normal);
}

.bosch-ic-small,
.Bosch-Ic-Small {
  @include make-icon-container($icon-font-size-small);
}

.Bosch-Ic-Disabled,
.bosch-ic-disabled,
.Bosch-Ic-Disabled:hover,
.bosch-ic-disabled:hover,
.Bosch-Ic[disabled],
.bosch-ic[disabled],
.Bosch-Ic[disabled]:hover,
.bosch-ic[disabled]:hover {
  cursor: not-allowed;
  color: $boschDarkGray25;

  &:hover {
    color: $boschDarkGray25;
  }
}

.bosch-ic-clickable,
.Bosch-Ic-Clickable {
  cursor: pointer;

  &:hover {
    color: $boschLightBlue50;
  }

  &:active {
    color: $boschLightBlue;
  }

  &:disabled, &.disabled {
    color: $boschDarkGray50;
    cursor: not-allowed;
  }
}

.bosch-ic-activated,
.Bosch-Ic-Activated {
  color: $boschLightBlue;
}


