@import '~@bci-web-core/common-styles/sass/bci/bci-variables';

//styling progress bar used for loading screen
mat-progress-bar.loading-screen-progress-bar {
  height: $gridSize;

  // set progress bar background color
  .mat-progress-bar-buffer {
    background-color: $boschLightGrayW75;
  }

  // set determinate progress bar fill
  .mat-progress-bar-fill {
    // reset initial transition, when loading screen opens
    transform: scaleX(0);

    // set fill gradient
    &::after {
      background-color: $boschDarkBlue;
      background-image: linear-gradient(to right, $boschDarkBlue 0%, $boschLightBlue 50%, $boschLightBlueW25 100%);
      background-size: cover;
      background-position: center center;
    }
  }

  // set indeterminate progress bar style
  &[mode="indeterminate"] {
    .mat-progress-bar-fill {
      transform: none;
    }

    // no animation
    .mat-progress-bar-primary {
      animation: none;
    }

    // no animation
    .mat-progress-bar-secondary {
      animation: none;
      left: 0;

      // set a gradient moving in a loop back and forth
      &.mat-progress-bar-fill::after {
        background: linear-gradient(to right, $boschDarkBlue, $boschLightBlue, $boschLightBlueW25, $boschLightBlue, $boschDarkBlue);
        background-size: 200% 200%;

        animation: mat-progress-bar-secondary-indeterminate-loop 3s ease infinite;

        @-webkit-keyframes mat-progress-bar-secondary-indeterminate-loop {
            0%{background-position:0% 50%}
            50%{background-position:100% 50%}
            100%{background-position:0% 50%}
        }
        @-moz-keyframes mat-progress-bar-secondary-indeterminate-loop {
            0%{background-position:0% 50%}
            50%{background-position:100% 50%}
            100%{background-position:0% 50%}
        }
        @keyframes mat-progress-bar-secondary-indeterminate-loop {
            0%{background-position:0% 50%}
            50%{background-position:100% 50%}
            100%{background-position:0% 50%}
        }
      }
    }
  }
}
