@import "../bci-variables";
@import "buttons";

@mixin make-chip-base {
  @include make-border(1px, $boschDarkGray);
  @include make-button-base($chip-height, $font-size-base, 0 $chip-padding-horizontal);
  display: inline-flex;
  cursor: default;
  color: inherit;
  background: inherit;
}

@mixin make-chip {
  @include make-chip-base;
  @include make-font-regular($font-size-base, 40px);
  color: $boschDarkGray;
  height: $chip-height;
}

@mixin make-chip-margin {
  margin: ($chip-margin-vertical/2) ($chip-margin-horizontal/2);
}

@mixin make-chip-list {
  display: flex;
  flex-wrap: wrap;

  & > * {
    @include make-chip-margin;
  }
}

@mixin make-chip-icon {
  text-align: center;
  box-sizing: border-box;
  display: inline-block;
  line-height: ($chip-height - 1px); /* -1px to compensate for line-height rendering issue */
  height: ($chip-height - 1px); /* -1px to compensate for line-height rendering issue */
  width: $chip-height;
  transform: translateY(0.5px);

  margin-right: -$chip-padding-horizontal;

  &:before {
    font-size: $icon-font-size-medium;
    line-height: $icon-font-size-medium;
  }

  &:hover {
    font-weight: bold;
  }

  &.bosch-ic-clickable:hover,
  &.bosch-ic-clickable:hover:active {
    font-weight: bold;
  }
}

@mixin make-chip-disabled {
  background-color: $boschLightGray;
  color: $boschWhite;
  border: none;
  outline: none;
  cursor: not-allowed;
}
