@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/icons';


@mixin tab-chevron($font-icon, $icon-size) {
  border: none;
  transform: none;
  width: $gridSize*4;
  height: $gridSize*4;
  &::before {
    @include make-icon($font-icon, $icon-size);
  }
}

// header
.mat-tab-header {
  border: none;
  border-bottom: 1px solid $boschLightGray75;

  mat-ink-bar.mat-ink-bar {
    height: 3px;
    background-color: $boschDarkBlue;
  }

  // set pagination button size based on view size
  // takes same size as:
  //  - first tab, left margin size
  //  - content padding size
  .mat-tab-header-pagination {
    min-width: $gridSize*6.5;
    &.mat-tab-header-pagination-disabled {
      min-width: $gridSize*4;
    }
    @media all and (max-width: $screen-sm-min) {
      min-width: $gridSize*4;
      &.mat-tab-header-pagination-disabled {
        min-width: $gridSize*2;
        .mat-tab-header-pagination-chevron {
          width: $gridSize*2;
        }
      }
    }
    @media all and (max-width: $screen-xs-min) {
      min-width: $gridSize*4;
      &.mat-tab-header-pagination-disabled {
        min-width: $gridSize*1.75;
      }
    }
  }
}

// set content size based on view size
.mat-tab-body .tab-content {
  padding: $gridSize*4;

  @media all and (max-width: $screen-sm-min) {
    padding: $gridSize*2
  }
  @media all and (max-width: $screen-xs-min) {
    padding: $gridSize*1.75
  }
}

// individual tabs settings
.mat-tab-links .mat-tab-link,
.mat-tab-labels .mat-tab-label {
  @include make-font-regular($font-size-h6);
  color: $font-color;
  line-height: $gridSize*6.5;
  height: $gridSize*6.5;
  padding: 0;
  text-align: left;
  min-width: $gridSize;
  opacity: 1;
  margin-right: $gridSize*3;

  &:hover {
    color: $boschDarkBlue;
  }

  &:focus {
    background-color: transparent !important;
  }

  &.mat-tab-label-active {
    color: $boschDarkBlue;
  }

  &.mat-tab-disabled {
    color: $boschDarkGray25;
  }

  .mat-ripple-element {
    display: none;
  }

  // set margin for tabs
  // first & last tabs have higher margins, between tabs there is a fixed size on all view sizes
  &:first-child {
    margin-left: $gridSize*4;
  }
  &:not(:last-child) {
    margin-right: $gridSize*3;
  }
  &:last-child {
    margin-right: $gridSize*4;
  }

  // set tabs margins (for first & last tabs) based on view size
  @media all and (max-width: $screen-sm-min) {
    &:first-child {
      margin-left: $gridSize*2;
    }
    &:last-child {
      margin-right: $gridSize*2;
    }
  }
  @media all and (max-width: $screen-xs-min) {
    &:first-child {
      margin-left: $gridSize*1.75;
    }
    &:last-child {
      margin-right: $gridSize*1.75;
    }
  }
}

// Use .no-margin class to mat-tab-group element to remove margin left from first tab
.mat-tab-group.no-margin .mat-tab-label:first-child,
.mat-dialog-content .mat-tab-labels .mat-tab-label:first-child {
  margin-left: 0;
}

// pagination enabled
.mat-tab-header-pagination-controls-enabled {
  // remove the margin, since the pagination controls are visible
  .mat-tab-links .mat-tab-link,
  .mat-tab-labels .mat-tab-label {
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }

  .mat-tab-header-pagination-before, .mat-tab-header-rtl .mat-tab-header-pagination-after {
    padding-left: 0;
    box-shadow: none;
    .mat-tab-header-pagination-chevron {
      @include tab-chevron($bosch-ic-back-left, $gridSize*4);
    }
  }
  .mat-tab-header-pagination-after, .mat-tab-header-rtl .mat-tab-header-pagination-before {
    padding-right: 0;
    box-shadow: none;
    .mat-tab-header-pagination-chevron {
      @include tab-chevron($bosch-ic-forward-right, $gridSize*4);
    }
  }
  .mat-tab-header-pagination-disabled .mat-tab-header-pagination-chevron {
    visibility: hidden;
  }
}
