@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';

mat-slider.mat-slider {
  $slider-default-color:  $boschDarkBlue;
  $slider-focus-color: $boschLightBlue;
  $slider-hover-color: $boschLightBlueW50;
  $slider-thumb-size: $gridSize * 3;

  padding-left: 0;
  padding-right: 0;
  margin-right: $slider-thumb-size;

  &.mat-slider-horizontal .mat-slider-wrapper {
    top: 22px;
    left: 0;
    right: 0;

    .mat-slider-track-wrapper {
      padding-right: $slider-thumb-size;
    }
  }

  .mat-slider-focus-ring {
    display: none;
  }

  &:not(.cdk-focused):hover {
    &.mat-accent {
      .mat-slider-thumb,
      .mat-slider-track-fill {
        background-color: $slider-hover-color;
      }
    }
  }

  &.mat-accent {
    .mat-slider-thumb,
    .mat-slider-track-fill {
      background-color:  $slider-default-color;
    }
  }

  &.mat-slider-disabled.mat-accent {
    .mat-slider-thumb,
    .mat-slider-track-fill {
      background-color: $boschLightGray;
    }

    .mat-slider-track-background {
      background-color: $boschLightGrayW25;
    }
  }

  .mat-slider-thumb-container .mat-slider-thumb-label {
    border: none;
    border-radius: unset;
    transform: rotate(0);
    width: 52px;
    height: auto;
    background-color: transparent;
    right: -38px;
    top: -54px;

    .mat-slider-thumb-label-text {
      @include make-font-regular(16px);
      transform: none;
      color: $boschWhite;
      padding: $gridSize / 2;
      background-color:  $slider-default-color;
    }
  }

  &.cdk-focused {
    .mat-slider-thumb,
    .mat-slider-thumb-container .mat-slider-thumb-label .mat-slider-thumb-label-text,
    .mat-slider-track-fill {
      background-color: $slider-focus-color;
    }
  }

  .mat-slider-thumb {
    width: $slider-thumb-size;
    height: $slider-thumb-size;
    transform: none !important;
    bottom: -11px;
    right: -$slider-thumb-size;
    border: none;
  }

  &.mat-slider-min-value.mat-slider-thumb-label-showing.cdk-focused {
    .mat-slider-thumb {
      background-color: $slider-focus-color;
    }
    .mat-slider-thumb-label {
      background-color: transparent !important;
    }
  }

  &.mat-slider-vertical {
    .mat-slider-thumb {
      right: -12px;
    }

    .mat-slider-thumb-container .mat-slider-thumb-label {
      top: auto;
      width: $gridSize;
    }
  }
}
