@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/icons';
@import '~@bci-web-core/common-styles/sass/bci/mixins/checkbox';
@import '~@bci-web-core/common-styles/sass/bci/mixins/vendor-prefixes';

mat-form-field.mat-form-field.mat-form-field-type-mat-select {

  .mat-form-field-wrapper {
    .mat-form-field-flex {
      position: relative;
      top: -1px;
    }
  }

  .mat-form-field-label {
    width: calc(100% - #{$gridSize*4.5});
  }

  &.mat-form-field-appearance-legacy.mat-form-field-can-float.mat-form-field-should-float .mat-form-field-label,
  &.mat-form-field-should-float .mat-form-field-label {
    width: calc(100% - #{$gridSize*6});
  }

  &.mat-form-field-disabled {
    cursor: not-allowed;

    .mat-select-trigger {
      cursor: not-allowed;
    }
  }

  .mat-select-trigger {
    @include make-border(0px 0px 1px 0px, $boschLightGray);
    background-color: $form-field-default-theme-background-color;
  }

  .mat-select {
    display: grid;
  }

  .mat-select-value {
    @include make-font-regular(16px);
    @include make-form-input-padding-without-label;
    padding-right: 0;
  }

  &.mat-form-field-can-float .mat-select-value {
    @include make-form-input-padding-with-label;
    padding-right: 0;
  }

  .mat-select-arrow-wrapper {
    padding: 0 $gridSize*1.5;
  }

  .mat-select-arrow,
  &:focus .mat-select-arrow {
    border: 0;
    height: inherit;
    padding-left: $gridSize*2;
    color: $font-color;

    &:before {
      @include make-icon($Bosch-Ic-arrow-expand-down, $gridSize*3);
      @include translate(0, -50%);
      position: absolute;
      display: block;
      right: $gridSize*1.5;
      line-height: $gridSize*6;
    }
  }

  &.mat-focused {
    .mat-select-trigger {
      background-color: $form-field-default-theme-focused-background-color;
    }

    .mat-form-field-label {
      &.mat-primary, &.mat-accent, &.mat-warn {
        color: $font-color;
      }
    }

    &.mat-primary, &.mat-accent, &.mat-warn {
      .mat-select-arrow {
        color: $font-color;
      }
    }
  }

  &.ng-valid.bci-form-field-success {
    .mat-select-arrow {
      color: $font-color;
    }

    .mat-select-trigger {
      @include make-border(0px 0px 1px 0px, $boschSignalGreen);
    }
  }

  &.ng-valid.bci-form-field-warning,
  .bci-form-field-warning {
    .mat-select-arrow {
      color: $font-color;
    }

    .mat-select-trigger {
      @include make-border(0px 0px 1px 0px, $boschSignalYellow);
    }
  }

  &.mat-form-field-invalid.ng-invalid,
  .bci-form-field-error {
    .mat-select-arrow {
      color: $font-color;
    }

    .mat-select-trigger {
      @include make-border(0px 0px 1px 0px, $boschSignalRed);
    }
  }
}

.mat-select-panel {
  mat-option.mat-option {
    @include make-font-regular(16px);
    @include make-form-input-padding-without-label;
    color: $font-color;

    &.mat-option-disabled {
      color: $dropdown-menu-disabled-color;
      cursor: not-allowed;
    }

    .mat-pseudo-checkbox,
    .mat-pseudo-checkbox-checked {
      width: $checkbox-size;
      height: $checkbox-size;
      @include make-border(0, transparent);
    }

    .mat-pseudo-checkbox::after {
      color: $boschWhite;
      top: 7px;
      left: 4px;
      width: 14px;
      height: 5px;
    }

    .mat-ripple {
      display: none;
    }
  }

  mat-option.mat-option:not(.mat-option-multiple) {
    padding-left: $gridSize*2;

    &:hover,
    &:hover.mat-active,
    &:hover.mat-selected {
      &:not(.mat-option-disabled) {
        background: $dropdown-menu-hover-bg-color;
        color: $dropdown-menu-hover-color;
      }
    }

    &.mat-active {
      background: $dropdown-menu-active-bg-color;
      color: $dropdown-menu-active-color;
    }

    &.mat-selected,
    &.mat-active.mat-selected {
      background: $dropdown-menu-selected-bg-color;
      color: $dropdown-menu-selected-color!important;
    }
  }

  mat-option.mat-option.mat-option-multiple {
    &:hover,
    &.mat-active,
    &.mat-selected {
      background-color: $boschWhite;
    }

    .mat-pseudo-checkbox {
      background-color: $checkbox-background-color;
    }

    .mat-pseudo-checkbox-disabled {
      background-color: $checkbox-background-color-disabled;
      cursor: not-allowed;
    }

    &:hover,
    &:hover.mat-active,
    &:hover.mat-selected {
      .mat-pseudo-checkbox:not(.mat-pseudo-checkbox-disabled) {
        background-color: $checkbox-background-color-hover;
      }
    }

    &.mat-active {
      .mat-pseudo-checkbox {
        background-color: $checkbox-background-color-active;
      }
    }

    .mat-pseudo-checkbox-checked {
      background-color: $checkbox-background-color-checked;
    }
  }

  mat-optgroup.mat-optgroup {
    &.mat-optgroup-disabled .mat-optgroup-label {
      color: $dropdown-menu-disabled-color;
      cursor: not-allowed;
    }

    .mat-optgroup-label {
      @include make-font-medium(12px);
      color: $font-color;
      padding: $gridSize*1.5-1 $gridSize*2 $gridSize*0.5+1 $gridSize*2;
      height: auto;
    }
  }
}
