@mixin make-border-color-grey {
  border-top-color: $boschDarkGray;
}
@mixin make-border-color-green {
  border-top-color: $boschSignalGreen;
}
@mixin make-border-color-blue {
  border-top-color: $boschLightBlue50;
}
@mixin make-border-color-yellow {
  border-top-color: $boschSignalYellow;
}
@mixin make-border-color-orange {
  border-top-color: $boschSignalOrange;
}
@mixin make-border-color-red {
  border-top-color: $boschSignalRed;
}

@mixin make-bci-tile-placeholder-text-component {
  display: grid;

  > span {
    white-space: nowrap;
    font-size: $font-size-small;
  }

  > span:first-of-type {
    color: $boschDarkGray50;
    height: 17px;
  }

  > span:last-of-type {
    Overflow: hidden;
    Text-overflow: ellipsis;
  }
}

@mixin make-bci-tile-textual-elements {
  display: flex;
  flex-wrap: wrap;

  > * {
    @include make-bci-tile-placeholder-text-component;
    margin: ($gridSize * 2) ($gridSize * 4) 0 0;
  }
}

@mixin make-bci-tile-action-elements {
  align-self: flex-end;
  display: flex;

  > :not(:last-child) {
    margin-right: $gridSize;
  }
}

@mixin make-bci-tile-large-text {
  @include make-bci-tile-placeholder-text-component;
  margin-top: $gridSize * 2;

  > :last-child {
    height: $gridSize * 4;
    white-space: unset;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
  }
}

@mixin make-bci-tile-content {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

@mixin make-bci-tile-content-double {
  > :first-child, > :last-child {
    @include make-bci-tile-content;
  }
}

@mixin make-bci-tile-title {
  @include make-bci-tile-placeholder-text-component;
  width: 100%;

  > span {
    width: 100%;
  }

  > span:last-of-type {
    font-size: $font-size-h4;
    line-height: 27px;
  }
}

@mixin make-bci-tile-header {
  display: flex;

  > * {
    align-self: flex-start;
  }
}

@mixin make-bci-tile {
  padding: ($gridSize * 3 - 4) ($gridSize * 3) ($gridSize * 3 - 1) ($gridSize * 3);
  width: 100%;
  height: $gridSize * 20;
  border-top: $gridSize solid;
  @include make-border-color-grey;
  background-color: $boschLightGrayW75;
  display: flex;
  flex: 1 0 ($gridSize * 37);
  flex-direction: column;
  justify-content: space-between;
}

@mixin make-bci-tile-view {
  display: flex;
  flex-flow: row wrap;
  margin-left: $gridSize * 4;
  > * {
    margin: ($gridSize * 4) ($gridSize * 4) 0 0;
  }
}
