@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/chips';

mat-chip-list.mat-chip-list {
  &:focus {
    outline: none;
  }

  .mat-chip-list-wrapper {
    @include make-chip-list;
    padding: 20px 16px 5px 6px;
    border-bottom: 1px solid $boschDarkGrayW75;
    margin: 0;

    &:focus {
      outline: none;
    }
    .mat-chip-input.mat-input-element {
      padding: 0 0 0 5px;
      margin: 0 5px 0 5px;
      border-bottom: none;
      caret-color: $boschDarkGray;
    }

    & + .mat-form-field-label-wrapper .mat-form-field-label {
      margin-left: 12px;
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.mat-form-field .mat-chip-input.mat-input-element:active:enabled,
.mat-form-field .mat-chip-input.mat-input-element.active,
.mat-form-field .mat-chip-input.mat-input-element:focus:enabled,
.mat-form-field.mat-focused .mat-form-field-label-wrapper .mat-form-field-label {
  background-color: transparent;
}

mat-chip, mat-basic-chip {
  &.mat-chip, &.mat-chip:not(.mat-basic-chip) {
    @include make-chip;
    line-height: $chip-height;
    height: $chip-height;

    &:focus {
      box-shadow: none;
    }

    & + & {
      @include make-chip-margin;
    }

    &.mat-standard-chip {
      &:after {
        background: transparent;
      }
      &:not(.mat-chip-disabled):active {
        box-shadow: none;
      }
    }

    .mat-icon, .mat-chip-remove.mat-icon {
      color: inherit;
      opacity: 1;
      @include make-chip-icon;
      margin-left: 7px;
      margin-right: -8px;
      line-height: 24px;
      height: 24px;
      width: 24px;
      &:hover {
        color: $boschLightBlueW50;
        opacity: 1;
      }
      &:hover:active {
        color: $boschLightBlue;
        opacity: 1;
      }
    }
    & .mat-ripple-element {
      display: none;
    }
  }
}

