@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';

mat-option.mat-option,
mat-checkbox.mat-checkbox {

  .mat-checkbox-layout {
    height: $checkbox-size;
    padding: 0;
    align-items: center;
  }

  .mat-checkbox-inner-container {
    width: $checkbox-size;
    height: $checkbox-size;
  }

  .mat-checkbox-frame {
    background-color: $checkbox-background-color;
    border-style: none;
    border-radius: 0;
  }

  &.mat-checkbox-checked .mat-checkbox-background {
    background-color: $checkbox-background-color-checked;
    border-radius: 0;
  }

  &.mat-checkbox-indeterminate .mat-checkbox-background {
    background-color: $checkbox-background-color-checked;
    border-radius: 0;
  }

  .mat-checkbox-disabled .mat-checkbox-frame,
  &.mat-checkbox.mat-checkbox-disabled .mat-checkbox-background {
    background-color: $checkbox-background-color-disabled;

    .mat-checkbox-checkmark-path {
      stroke: $checkbox-stroke-color-disabled !important;
    }

    .mat-checkbox-mixedmark {
      background-color: $boschDarkGray50;
    }
  }

  .mat-checkbox-mixedmark {
    background-color: $checkbox-stroke-color;
   // d: path('M4.1,44.7 9,2.6 12.3,6.3');  @todo: set rectangle path for three state
  }
  .mat-checkbox-checkmark-path {
    stroke: $checkbox-stroke-color !important;
  }

  .mat-checkbox-label {
    @include make-radiobox-checkbox-label();
  }

  h3 {
    margin: 0;
  }

  .mat-checkbox-ripple {
    display: none;
  }
}
