@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/forms';

mat-form-field.mat-form-field {
  box-shadow: none;
  padding-bottom: 16px;

  .mat-form-field-label {
    background-color: $form-field-default-theme-background-color;
    width: calc(100% - #{$gridSize*2});
    left: 1px;
    padding-left: $gridSize*2 - 1;
    transition: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &.mat-form-field-has-label.mat-form-field-can-float.mat-focused .mat-input-element:focus+.mat-form-field-label-wrapper .mat-form-field-label {
    color: $font-color;
  }

  .mat-form-field-flex {
    background-color: $form-field-default-theme-background-color;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: flex-end;
  }

  .mat-form-field-infix {
    padding: 0;
    border: 0;
  }

  textarea {
    @include make-textarea;
    margin: 0;
  }

  input {
    @include make-form-input;
    margin: 0;
  }

  &.mat-form-field-disabled.mat-form-field-should-float .mat-form-field-label {
    color: $form-field-placeholder-color;
  }

  &.mat-form-field-disabled .mat-form-field-label-wrapper .mat-form-field-label {
    color: $form-field-placeholder-color;
    background-color: $form-field-default-theme-disabled-background-color;
  }

  &.mat-form-field-can-float .mat-input-element {
    padding-top: $gridSize*2.5;
    padding-bottom: $gridSize*0.5;
  }

  .mat-form-field-label-wrapper {
    top: 0;
    padding-top: 0;
    vertical-align: middle;
  }

  .mat-form-field-label,
  &.mat-focused .mat-form-field-label {
    box-sizing: border-box;
    padding: 0 $gridSize*2 - 2;
    color: $form-field-default-theme-focused-placeholder-color;
    top: 12px;
    left: 2px;
    vertical-align: middle;
    @include make-font-regular(16px);

    .mat-form-field-required-marker {
      color: $font-color;
    }
  }

  // fix for autofill
  // needs to be kept separate, if this and next selectors are merged, then Mozilla will ignore it completely and label will appear broken
  &.mat-form-field-appearance-legacy.mat-form-field-can-float input.mat-form-field-autofill-control:-webkit-autofill+.mat-form-field-label-wrapper .mat-form-field-label {
    @include make-font-medium($font-size-small);
    transform: translateY(0) scale(1) perspective(100px) translateZ(0px);
    padding: $gridSize*0.5 0 0 $gridSize*2-1;
    width: calc(100% - #{$gridSize*3});
    top: 1px;
  }

  // styling the label
  &.mat-form-field-appearance-legacy.mat-form-field-can-float.mat-form-field-should-float .mat-form-field-label,
  &.mat-form-field-should-float .mat-form-field-label {
    @include make-font-medium($font-size-small);
    transform: translateY(0) scale(1) perspective(100px) translateZ(0px);
    padding: $gridSize*0.5 0 0 $gridSize*2-1;
    width: calc(100% - #{$gridSize*3});
    top: 1px;
  }

  // when input field is autofilled, it's label needs to be transparent due to browser placing a background color marked as !important
  &.mat-form-field-appearance-legacy.mat-form-field-can-float input.mat-form-field-autofill-control+.mat-form-field-label-wrapper .mat-form-field-label {
    background-color: transparent;
  }

  &.mat-form-field-should-float .mat-form-field-label {
    color: $font-color;
  }

  .mat-form-field-subscript-wrapper {
    @include make-font-medium(12px);
    color: $form-field-placeholder-color;
    position: relative;
    z-index: 0;
    display: table;
    top: 0;
    padding: 0 $gridSize*2;
    margin-top: -$gridSize*2.25;
    width: 100%;

    .mat-hint,
    .mat-error {
      margin-top: $gridSize*2.25;
      padding-top: $gridSize*0.5;
      padding-bottom: $gridSize*0.5-1;

      &:empty {
        display: none;
      }
    }

    .mat-form-field-hint-wrapper {
      display: inline-flex;
      width: 100%;
    }

    .mat-icon {
      height: $gridSize*3;
      width: $gridSize*3;
    }
  }

  .mat-form-field-underline {
    display: none;
  }

  .mat-form-field-wrapper {
    padding-bottom: 0;
  }

  .mat-button-ripple {
    width: $button-size-small;
    height: $button-size-small;
    top: -$gridSize;
  }

  .mat-form-field-prefix {
    padding: 0 0 0 $gridSize * 2;
  }

  .mat-form-field-suffix {
    padding: 0 $gridSize * 2 0 0;
  }

  .mat-form-field-prefix,
  .mat-form-field-suffix {
    @include make-font-regular(16px);
    @include make-border(0px 0px 1px 0px, $boschLightGray);
    height: $form-field-size-base;

    > * {
      display: block;
      padding-top: $gridSize * 1.5;
      padding-bottom: $gridSize * 1.5;
    }

    > .align-to-content {
      padding-top: $padding-large-vertical * 2;
      padding-bottom: $padding-small-vertical - 1;
    }

    .mat-icon,
    .mat-icon-button .mat-icon {
      vertical-align: middle;
      line-height: 0.9;

      // fix font size of the material icons
      &.material-icons {
        font-size: $gridSize*3;
      }
    }

    .mat-icon-button .mat-icon {
      line-height: 0.9;
    }

    .mat-icon-button {
      width: $gridSize*6;

      // reduce the margins, as buttons needs to ocupy no more than 48px
      &:first-child {
        margin-left: -$padding-base-horizontal;
      }

      &:last-child {
        margin-right: -$padding-base-horizontal;
      }

      // fixing icon wrapper and ripple effect
      .mat-button-wrapper,
      .mat-button-ripple,
      .mat-button-focus-overlay {
        width: $gridSize*6;
        height: $gridSize*6;
        top: -$gridSize*1.5;
      }

      .mat-icon {
        height: $gridSize*3;
        width: $gridSize*3;
        margin: 0;
      }
    }


  }

  &.mat-focused .mat-form-field-prefix,
  &.mat-focused .mat-form-field-suffix {
    background-color: $form-field-default-theme-focused-background-color;
  }

  &.mat-form-field-invalid.ng-invalid,
  &.ng-valid,
  .bci-form-field-success,
  .bci-form-field-warning,
  .bci-form-field-error {

    .mat-form-field-label,
    .mat-form-field-label .mat-form-field-required-marker {
      color: $form-field-placeholder-color;
    }

    &.mat-form-field-should-float .mat-form-field-label,
    .mat-form-field-label .mat-form-field-required-marker {
      color: $font-color;
    }

    input,
    textarea {
      caret-color: $form-field-caret-color;
    }
  }

  &.ng-valid.bci-form-field-success {

    input,
    textarea,
    .mat-form-field-prefix,
    .mat-form-field-suffix {
      @include make-border(0px 0px 1px 0px, $boschSignalGreen);
    }

    textarea {
      @include make-border(1px, $boschSignalGreen);
    }

    .mat-form-field-subscript-wrapper,
    .mat-hint {
      color: $boschSignalGreen;
    }
  }


  &.ng-valid.bci-form-field-warning,
  .bci-form-field-warning {

    input,
    textarea,
    .mat-form-field-prefix,
    .mat-form-field-suffix {
      @include make-border(0px 0px 1px 0px, $boschSignalYellow);
    }

    textarea {
      @include make-border(1px, $boschSignalYellow);
    }

    .mat-form-field-subscript-wrapper,
    .mat-hint,
    .mat-error.bci-text-warning {
      color: $boschSignalYellow;
    }
  }

  &.mat-form-field-invalid.ng-invalid,
  .bci-form-field-error {

    input,
    .mat-form-field-prefix,
    .mat-form-field-suffix {
      @include make-border(0px 0px 1px 0px, $boschSignalRed);
    }

    textarea {
      @include make-border(1px, $boschSignalRed);
    }

    .mat-form-field-subscript-wrapper {
      color: $boschSignalRed;
    }
  }

  mat-error:not(:first-of-type) {
    display: none;
  }
}
