@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/table-cells';

// Table Rows
@function getMatStateSelector($state) {
  @if ($state == '') {
    @return '';
  }
  @return '.#{$state}';
}

$tables: (
  mat-table: (
    row: mat-row,
    cell: mat-cell
  ),
  table: (
    row: 'tbody > tr',
    cell: td
  ),
);

@mixin mat-table-row-variant($element, $state) {
  $stateSelector: getMatStateSelector($state);
  $elem: map-get($tables, $element);

  > #{map-get($elem, row)}.mat-row {
    > #{map-get($elem, cell)}.mat-cell#{$stateSelector},
    &#{$stateSelector} > #{map-get($elem, cell)}.mat-cell {
      @content
    }
  }
}

@mixin mat-table-row-variant-hover($element, $state) {
  $stateSelector: getMatStateSelector($state);
  $elem: map-get($tables, $element);

  > #{map-get($elem, row)}.mat-row {
    > #{map-get($elem, cell)}.mat-cell#{$stateSelector}:hover,
    &#{$stateSelector}:hover > #{map-get($elem, cell)}.mat-cell:not(.mat-table-sticky) {
      @content
    }
  }
}

@mixin mat-table-row-variant-stripes($element, $state) {
  $stateSelector: getMatStateSelector($state);
  $elem: map-get($tables, $element);

  > #{map-get($elem, row)}.mat-row {
    > #{map-get($elem, cell)}.mat-cell#{$stateSelector}:nth-of-type(even),
    &#{$stateSelector} > #{map-get($elem, cell)}.mat-cell:nth-of-type(even) {
      @content
    }
  }
}

@mixin mat-table-row-variant-stripes-hover($element, $state) {
  $stateSelector: getMatStateSelector($state);
  $elem: map-get($tables, $element);

  > #{map-get($elem, row)}.mat-row {
    > #{map-get($elem, cell)}.mat-cell#{$stateSelector}:nth-of-type(even):hover,
    &#{$stateSelector}:hover > #{map-get($elem, cell)}.mat-cell:nth-of-type(even) {
      @content
    }
  }
}

@mixin mat-table-row-variant-stripes-horizontal($element, $state) {
  $stateSelector: getMatStateSelector($state);
  $elem: map-get($tables, $element);

  > #{map-get($elem, row)}.mat-row:nth-of-type(even) {
    > #{map-get($elem, cell)}.mat-cell#{$stateSelector},
    &#{$stateSelector} > #{map-get($elem, cell)}.mat-cell {
      @content;
    }
  }

  > #{map-get($elem, row)}.mat-row:nth-of-type(odd) {
    &.selected > #{map-get($elem, cell)}.mat-cell {
      @content;
      // Temporary fix
      border-bottom: none !important;
    }
  }
}

@mixin mat-table-row-variant-stripes-horizontal-hover($element, $state) {
  $stateSelector: getMatStateSelector($state);
  $elem: map-get($tables, $element);

  > #{map-get($elem, row)}.mat-row {
    &:nth-of-type(even):hover > #{map-get($elem, cell)}.mat-cell#{$stateSelector},
    &#{$stateSelector}:hover > #{map-get($elem, cell)}.mat-cell {
      @content;
    }
  }
}

@mixin mat-tables-row-variant-link($state) {
  $placeholder: getTableCellStylePlaceholder($state);

  @each $tableKey, $tableValue in $tables {
    #{$tableKey}.mat-table:not(.table-striped-horizontal) {
      @include mat-table-row-variant($tableKey, $state) {
        @extend %#{$placeholder};
      }
      @include mat-table-row-variant-hover($tableKey, $state) {
        @extend %#{$placeholder}-hover;
      }
      @include mat-table-row-variant-stripes($tableKey, $state) {
        @extend %#{$placeholder}-stripes;
      }
      @include mat-table-row-variant-stripes-hover($tableKey, $state) {
        @extend %#{$placeholder}-stripes-hover;
      }
    }
    #{$tableKey}.mat-table.table-striped-horizontal {
      @include mat-table-row-variant-stripes-horizontal($tableKey, $state) {
        @extend %#{$placeholder}-stripes-horizontal;
      }
      @include mat-table-row-variant-stripes-horizontal-hover($tableKey, $state) {
        @extend %#{$placeholder}-stripes-horizontal-hover;
      }
    }
  }
}

mat-table.mat-table {
  box-shadow: none !important;

  mat-header-row.mat-header-row {
    border-bottom: $table-header-border-width solid $table-border-color;
    min-height: 44px;
    padding: 0;

    mat-header-cell.mat-header-cell {
      text-align: left;
      color: $font-color;
      padding: 0 16px;
      @include make-font-bold;
      align-self: stretch;
    }
  }

  mat-row.mat-row {
    $min-value: 31px;
    padding: 0;
    min-height: $min-value;

    mat-cell.mat-cell {
      line-height: $line-height-base;
      padding: 8px 16px;
      display: flex;
      align-self: stretch;
      align-items: center;
      min-height: $min-value;
      &.mat-table-sticky:first-child {
        background-color: $boschWhite;
      }
      &.mat-column-actions .mat-icon-button {
        height: $min-value;
        line-height: $min-value;
        min-width: $min-value;
        .mat-button-wrapper {
          min-width: $min-value;
          height: auto;
          .mat-icon {
            margin: 0 0 0 1px;
          }
        }
        .mat-button-ripple.mat-ripple,
        .mat-button-focus-overlay {
          width: $min-value;
        }
      }
    }

    &.selected > .mat-cell {
      &.mat-table-sticky:first-child {
        background-color: $boschLightBlueW75;
        &:hover {
          background-color: $boschDarkBlueW75;
        }
      }
    }
  }
}

table.mat-table {
  box-shadow: none !important;
  border-collapse: collapse;

  tr.mat-header-row {
    border-bottom: $table-header-border-width solid $table-border-color;
    height: 48px;
    padding: 0;

    th.mat-header-cell {
      text-align: left;
      color: $font-color;
      padding: 0 16px;
      @include make-font-bold;
    }
  }

  tr.mat-row {
    padding: 0;

    td.mat-cell {
      line-height: $line-height-base;
      padding: 8px 16px;
    }
  }
}

@each $tableRowState in $tableRowStates {
  @include mat-tables-row-variant-link($tableRowState);
}

