@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/icons';

mat-expansion-panel.mat-expansion-panel,
mat-expansion-panel.mat-expansion-panel:not([class*=mat-elevation-z]) {
  box-shadow: none;
  @include make-border(1px 0 0 0, $boschLightGrayW50);

  &:first-of-type {
    border-radius: 0;
  }
  &:last-of-type {
    @include make-border(1px 0 1px 0, $boschLightGrayW50);
  }

  &:hover {
    background: transparent;
  }

  &.mat-expansion-panel-spacing {
    margin: 0;
  }

  .mat-expansion-panel-header {
    padding: 0 8px 0 0;
    border: none;
    font-size: $font-size-h5;

    .mat-expansion-indicator {
      &::after {
        @include make-icon($Bosch-Ic-arrow-expand-down, 22px);
        transform: none;
        border: none;
      }
    }

    .mat-expansion-panel-header-title,
    .mat-expansion-panel-header-description,
    .mat-expansion-panel-header .mat-content {
      font-size: $font-size-h5;
    }
  }

  .mat-expansion-panel-content {

    .mat-expansion-panel-body {
      padding: 0 0 16px 0;
    }

    .mat-action-row {
      border: none;
    }
  }

}
