@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';

.mat-stepper-horizontal {
  .mat-step-header.cdk-keyboard-focused, .mat-step-header.cdk-program-focused {
    background: transparent;
  }

  .mat-step-icon.mat-step-icon {
    background: $boschLightGray50;
  }

  .mat-step-label.mat-step-label {
    color: $boschLightGray50;
    font-size: $font-size-medium;
  }

  .mat-step-header .mat-step-icon-selected,
  .mat-step-header .mat-step-icon-state-done,
  .mat-step-header .mat-step-icon-state-edit,
  .mat-step-header[aria-selected='true'] .mat-step-icon {
    background: $boschDarkBlue;
  }

  .mat-horizontal-stepper-header[aria-selected='true'] {
    &:not(:last-child)::after,
    &:not(:first-child)::before,
    & + .mat-stepper-horizontal-line {
      border-top-color: $boschDarkBlue;
    }

    & + .mat-stepper-horizontal-line::after {
      border-top-color: $boschLightGray50;
    }
  }

  .mat-stepper-horizontal-line::after {
    content: '';
    width: 50%;
    display: block;
    position: absolute;
    top: -2px;
    right: 0;
    border-top-width: 2px;
    border-top-style: solid;
    border-top-color: transparent;
  }

  .mat-step-header .mat-step-label.mat-step-label-active {
    color: $boschDarkBlue;
  }

  .mat-horizontal-stepper-header.mat-horizontal-stepper-header {
    &:not(:last-child)::after,
    &:not(:first-child)::before,
    & + .mat-stepper-horizontal-line {
      border-top-width: 2px;
    }

    & + .mat-stepper-horizontal-line {
      flex-grow: 1;
    }
  }

  @for $i from 0 through 9 {
    &.stepper--step0#{$i}-active {
      .mat-horizontal-stepper-header:nth-of-type(#{$i+1}) {
        &:not(:last-child)::after,
        &:not(:first-child)::before {
          border-top-color: $boschDarkBlue;
        }
      }

      /* Active headers that are preceded by the selected header (e.g. after navigating backwards) */
      .mat-horizontal-stepper-header[aria-selected='true'] ~ .mat-horizontal-stepper-header:nth-of-type(#{$i+1}) {
        &:not(:last-child)::after,
        &:not(:first-child)::before {
          border-top-color: $boschDarkBlue50;
        }

        &.mat-step-header .mat-step-label.mat-step-label-active {
          color: $boschDarkBlue50;
        }

        &.mat-step-header .mat-step-icon-selected,
        &.mat-step-header .mat-step-icon-state-done,
        &.mat-step-header .mat-step-icon-state-edit {
          background: $boschDarkBlue50;
        }

        & + .mat-stepper-horizontal-line {
          border-top-color: $boschDarkBlue50;
        }

        & + .mat-stepper-horizontal-line::after {
          border-top-color: $boschLightGray50;
        }
      }

      /* lines in beteween */
      .mat-stepper-horizontal-line:nth-of-type(#{$i}) {
        border-top-color: $boschDarkBlue;
      }

      .mat-stepper-horizontal-line:nth-of-type(#{$i+1}) {
        border-top-color: $boschDarkBlue;
      }

      .mat-horizontal-stepper-header[aria-selected='true'] + .mat-stepper-horizontal-line:nth-of-type(#{$i}) {
        border-top-color: $boschDarkBlue;

        &::after {
          border-top-color: $boschDarkBlue50;
        }
      }

      .mat-horizontal-stepper-header[aria-selected='true'] ~ .mat-horizontal-stepper-header:nth-of-type(#{$i}) {
        & + .mat-stepper-horizontal-line {
          border-top-color: $boschDarkBlue50;
        }

        & + .mat-stepper-horizontal-line::after {
          border-top-color: $boschDarkBlue50;
        }
      }

      /* lines in beteween - end */
    }
  }

  /* vertical alignment */
  &.mat-stepper-label-position-bottom .mat-horizontal-stepper-header {
    padding: 3*$gridSize 0px;
    min-width: 8em;
  }

  .mat-horizontal-stepper-header-container {
    padding-bottom: 3*$gridSize;
  }

  &.mat-stepper-label-position-bottom {
    .mat-horizontal-stepper-header:not(:last-child)::after,
    .mat-horizontal-stepper-header:not(:first-child)::before,
    .mat-stepper-horizontal-line {
      top: 5.5*$gridSize;
    }

    .mat-horizontal-stepper-header .mat-step-label {
      padding-top: $gridSize;
    }
  }

  .mat-step-icon {
    width: 5*$gridSize;
    height: 5*$gridSize;
  }

  .mat-step-icon .mat-icon {
    width: 3*$gridSize;
    height: 3*$gridSize;
  }

  /* vertical alignment - end */
}
