@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';
@import '~@bci-web-core/common-styles/sass/bci/mixins/buttons';


button.mat-raised-button,
button.mat-raised-button.mat-accent {
  @include make-primary-button;
  box-shadow: none;
  min-width: $gridSize*12;

  &:hover,
  &:active,
  &:not([disabled]):active,
  &.active,
  &:focus,
  &.focus{
    box-shadow: none;
    outline: none;
  }

  &[disabled],
  fieldset[disabled] {
    //cursor: not-allowed;
  }
}

button {
  &.mat-fab:not([class*=mat-elevation-z]),
  &.mat-mini-fab:not([class*=mat-elevation-z]),
  &.mat-raised-button:not([class*=mat-elevation-z]),
  &.mat-fab:not([disabled]):active:not([class*=mat-elevation-z]),
  &.mat-mini-fab:not([disabled]):active:not([class*=mat-elevation-z]),
  &.mat-raised-button:not([disabled]):active:not([class*=mat-elevation-z])
  {
    box-shadow: none;
  }
}


button.mat-button {
  @include make-secondary-button;
  min-width: $gridSize*12;

  .mat-button-focus-overlay {
    background-color: transparent !important;
  }
}

a.mat-button {
  @include make-link-button;

  .mat-button-focus-overlay {
    background-color: transparent !important;
  }

  .mat-button-ripple {
    display: none;
  }
}

button.mat-icon-button {
  width: auto;
  min-width: $button-size-base;
  height: $button-size-base;
  text-align: left;

  .mat-button-wrapper {
    min-width: $button-size-base;
    height: $button-size-base;
    display: inline-block;
    text-align: center;
  }

  .mat-ripple, .mat-button-focus-overlay {
    width: $button-size-base;
  }
}

button, a.mat-button {
  .mat-icon {
    margin: $gridSize*1.5;
  }
}

button:not(.mat-icon-button) .mat-button-wrapper {
  & > .mat-icon:last-child {
    margin-right: 0;
    margin-left: 8px;
  }

  & > .mat-icon:first-child {
    margin-left: 0;
    margin-right: 8px;
  }

  & > .mat-icon:only-child {
    margin: 0 $gridSize - 4px; /* 4px less due to width of potential space between icon and label */
  }
}

.mat-icon-button .mat-button-wrapper :last-child:not(.mat-icon) {
  margin-right: 2 * $gridSize;
}

button.mat-fab{
  width: $gridSize*6;
  height: $gridSize*6;

  .mat-button-wrapper {
    padding: $gridSize*1.5 0;
  }
}