// Functional colors
$boschSignalYellow: $boschYellow;
$boschSignalRed: $boschRed;
$boschSignalGreen: $boschLightGreen;

// gradient pairs
$gradient-pair-variations:(
        fuchsia-violet:($boschFuchsia, $boschViolet),
        red-fuchsia:($boschRed, $boschFuchsia),
        violet-darkBlue:($boschViolet, $boschDarkBlue),
        darkBlue-lightBlue:($boschDarkBlue, $boschLightBlue),
        lightBlue-turquoise:($boschLightBlue, $boschTurquoise),
        turquoise-lightGreen: ($boschTurquoise, $boschLightGreen),
        lightGreen-darkGreen: ($boschLightGreen, $boschDarkGreen),
);

// deprecated colors (left here for backwards compatibility and partially mapped to new color gradients)
