$checkbox-size: $input-form-size;

$checkbox-background-color: $boschLightGray50;
$checkbox-background-color-checked: $boschDarkBlue;
$checkbox-background-color-active: $boschLightBlue;
$checkbox-background-color-hover: $boschLightBlueW50;
$checkbox-stroke-color: $boschWhite;

$checkbox-background-color-disabled: $boschLightGray25;
$checkbox-stroke-color-disabled: $boschLightGray;
$checkbox-label-color-disabled: $boschLightGray;

@mixin make-radiobox-checkbox-label {
    font-weight: $font-weight-regular;
    font-size: $font-size-base;
    height: $input-form-size;
    line-height: $input-form-size;
}
