@import "../bci-variables";

@mixin make-scrollbars() {

  /* width and height */
  &::-webkit-scrollbar {
    width: $gridSize / 2;
    height: $gridSize / 2;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: transparent;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: $boschDarkGrayW25;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: $boschDarkGrayB25;
  }
}

@mixin make-scrollbars-global() {

  /* width and height */
  ::-webkit-scrollbar {
    width: $gridSize / 2;
    height: $gridSize / 2;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: transparent;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: $boschDarkGrayW25;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: $boschDarkGrayB25;
  }
}
