@import '~@bci-web-core/common-styles/sass/bci-theme';

mat-slide-toggle.mat-slide-toggle {
    
    .mat-slide-toggle-bar {
        @include make-toggle-bar-base();
    }
    
    .mat-slide-toggle-thumb-container {
        @include make-toggle-thumb-container-base();
    }
    
    .mat-slide-toggle-thumb {
        @include make-toggle-thumb-base();
    }
    
    .mat-slide-toggle-content {
        @include make-radiobox-checkbox-label();
    }
    
    
    
    &.mat-checked,
    &.mat-checked:not(.mat-disabled),
    &.mat-checked.mat-primary:not(.mat-disabled){
        .mat-slide-toggle-bar {
            @include make-toggle-bar-active();
        }
        
        .mat-slide-toggle-thumb-container {
            @include make-toggle-thumb-container-active();
        }
        
        .mat-slide-toggle-thumb {
            @include make-toggle-thumb-active();    
        }
    }
    
    &.mat-disabled,
    &.mat-checked.mat-disabled{
        .mat-slide-toggle-bar {
            @include make-toggle-disabled-background();
        }
        
        .mat-slide-toggle-thumb {
            @include make-toggle-disabled-foreground();
        }
    }
}



