@mixin make-toggle-bar-base() {
    width: $input-form-size * 2;
    height: $input-form-size;
    border-radius: 12px;
    background-color: $boschLightGrayW50;
    transition: $transition-time-quarter;
}

@mixin make-toggle-bar-active() {
    background-color: $boschDarkBlue;
}

@mixin make-toggle-thumb-container-base() {
    width: $input-form-size - 4px;
    height: $input-form-size - 4px;
    transition: $transition-time-quarter;
    top: 2px;
    left: $input-form-size / 12;
}

@mixin make-toggle-thumb-container-active() {
    transform: translateX($input-form-size);
}

@mixin make-toggle-thumb-base() {
    width: $input-form-size - 4px;
    height: $input-form-size - 4px;
    box-shadow: none;
    border-radius: 50%;
    background-color: $boschWhite;
}

@mixin make-toggle-thumb-active() {
    background-color: $boschWhite;
}

@mixin make-toggle-disabled-background() {
    background-color: $boschLightGrayW50;
    cursor: not-allowed;
}

@mixin make-toggle-disabled-foreground() {
    background-color: $boschLightGray;
}

