@font-face {
  font-family: $font-family-base;
  font-weight: 400;
  src: url('#{$path-to-assets}/fonts/BoschSans-Regular.eot'); /* IE9 Compat Modes */
  src: url('#{$path-to-assets}/fonts/BoschSans-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('#{$path-to-assets}/fonts/BoschSans-Regular.woff2') format('woff2'), /* Super Modern Browsers */
  url('#{$path-to-assets}/fonts/BoschSans-Regular.woff') format('woff'), /* Pretty Modern Browsers */
  url('#{$path-to-assets}/fonts/BoschSans-Regular.ttf') format('truetype'), /* Safari, Android, iOS */
  url('#{$path-to-assets}/fonts/BoschSans-Regular.svg#svgFontName') format('svg'); /* Legacy iOS */
}

