@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';

mat-radio-button.mat-radio-button {
  @include make-font;

  .mat-radio-container,
  .mat-radio-outer-circle,
  .mat-radio-inner-circle {
    width: $checkbox-size;
    height: $checkbox-size;
  }

  &.mat-primary,
  &.mat-accent,
  &.mat-warn {
    .mat-radio-outer-circle {
      background-color: $radiobox-outer-color;
      border-width: 0;
    }
  }

  &.mat-radio-checked,
  &.mat-accent.mat-radio-checked{
    .mat-radio-outer-circle {
      border-color: $radiobox-outer-color-checked;
      border-width: $checkbox-size/4;
    }

    .mat-radio-inner-circle {
      background-color: $radiobox-inner-color-checked;
    }
  }

  &.mat-radio-disabled,
  &.mat-radio-checked.mat-radio-disabled{
    .mat-radio-outer-circle {
      border-color: $radiobox-outer-color-disabled;
      background-color: $radiobox-outer-color-disabled;
    }

    .mat-radio-inner-circle {
      background-color: $radiobox-inner-color-disabled;
    }
  }

  .mat-radio-label {
    @include make-radiobox-checkbox-label();
  }

  .mat-radio-ripple {
    display: none;
  }

}
