.chip {
  @include make-chip;

  .chip-icon {
    @include make-chip-icon;
  }

  &:not(.disabled) {
    height: $chip-height - 2;
  }

  &.disabled {
    @include make-chip-disabled;
  }
}

.chip-list {
  @include make-chip-list;
}
