@mixin make-indicator-overlay($size: 72px, $animation-duration: 10s, $square-size: $gridSize * 3) {
  z-index: 10000;
  width: $size;
  height: $size;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  @content;

  &::before {
    content: " ";
    width: $square-size;
    height: $square-size;
    position: absolute;
    animation: top-right $animation-duration infinite;
  }

  &::after {
    content: " ";
    width: $square-size;
    height: $square-size;
    position: absolute;
    animation: bottom-left $animation-duration infinite;
  }
  
  @keyframes bottom-left {
    0% {
      background-color: transparent;
      left: $square-size;
      top: $square-size;
    }
    6.25% {
      background-color: $boschViolet;
      left: $square-size;
      top: $square-size;
    }
    12.5% {
      background-color: $boschViolet;
      left: $square-size;
      top: $square-size * 2;
    }
    18.75% {
      background-color: $boschRed;
      left: $square-size;
      top: $square-size;
    }
    25% {
      background-color: $boschRed;
      left: 0;
      top: $square-size;
    }
    31.25% {
      background-color: $boschDarkGreen;
      left: $square-size;
      top: $square-size;
    }
    37.5% {
      background-color: $boschDarkGreen;
      left: $square-size;
      top: $square-size * 2;
    }
    43.75% {
      background-color: $boschLightGreen;
      left: $square-size;
      top: $square-size;
    }
    50% {
      background-color: $boschLightGreen;
      left: 0;
      top: $square-size;
    }
    56.25% {
      background-color: $boschTurquoise;
      left: $square-size;
      top: $square-size;
    }
    62.5% {
      background-color: $boschTurquoise;
      left: $square-size;
      top: $square-size * 2;
    }
    68.75% {
      background-color: $boschLightBlue;
      left: $square-size;
      top: $square-size;
    }
    75% {
      background-color: $boschLightBlue;
      left: 0;
      top: $square-size;
    }
    81.25% {
      background-color: $boschDarkBlue;
      left: $square-size;
      top: $square-size;
    }
    87.5% {
      background-color: $boschDarkBlue;
      left: $square-size;
      top: $square-size * 2;
    }
    93.75% {
      background-color: $boschFuchsia;
      left: $square-size;
      top: $square-size;
    }
    100% {
      background-color: transparent;
      left: $square-size;
      top: $square-size;
    }
  }

  @keyframes top-right {
    0% {
      background-color: transparent;
      left: $square-size;
      top: $square-size;
    }
    6.25% {
      background-color: $boschViolet;
      left: $square-size;
      top: $square-size;
    }
    12.5% {
      background-color: $boschFuchsia;
      left: $square-size;
      top: 0;
    }
    18.75% {
      background-color: $boschRed;
      left: $square-size;
      top: $square-size;
    }
    25% {
      background-color: $boschViolet;
      left: $square-size * 2;
      top: $square-size;
    }
    31.25% {
      background-color: $boschDarkGreen;
      left: $square-size;
      top: $square-size;
    }
    37.5% {
      background-color: $boschRed;
      left: $square-size;
      top: 0;
    }
    43.75% {
      background-color: $boschLightGreen;
      left: $square-size;
      top: $square-size;
    }
    50% {
      background-color: $boschDarkGreen;
      left: $square-size * 2;
      top: $square-size;
    }
    56.25% {
      background-color: $boschTurquoise;
      left: $square-size;
      top: $square-size;
    }
    62.5% {
      background-color: $boschLightGreen;
      left: $square-size;
      top: 0;
    }
    68.75% {
      background-color: $boschLightBlue;
      left: $square-size;
      top: $square-size;
    }
    75% {
      background-color: $boschTurquoise;
      left: $square-size * 2;
      top: $square-size;
    }
    81.25% {
      background-color: $boschDarkBlue;
      left: $square-size;
      top: $square-size;
    }
    87.5% {
      background-color: $boschLightBlue;
      left: $square-size;
      top: 0;
    }
    93.75% {
      background-color: $boschFuchsia;
      left: $square-size;
      top: $square-size;
    }
    100% {
      background-color: transparent;
      left: $square-size;
      top: $square-size;
    }
  }
}
