//-------------------------------------------------------------------------------------------------------------- NAVIGATION BUTTONS
@mixin make-pagination-button-previous($font-size) {
  &::after {
    @include make-icon($Bosch-Ic-arrow-expand-left, $font-size);
  }
}

@mixin make-pagination-button-next($font-size) {
  &::after {
    @include make-icon($Bosch-Ic-arrow-expand-right, $font-size);
  }
}

//-------------------------------------------------------------------------------------------------------------- BASIC ELEMENT STYLES
@mixin reset-pagination-element-pre-defined-styles {
  margin: 0;
  padding: 0;
  line-height: $line-height-base;
  color: $font-color;
  border: none;
  background: none;
  text-decoration: none;
}

@mixin make-pagination-element($base-size, $font-size) {
  cursor: pointer;
  font-size: $font-size;
  display: inline-block;
  min-width: $base-size;
  width: $base-size;
  height: $base-size;
  line-height: $base-size;
  border-radius: 50%;
  text-align: center;
  margin-right: 4px;
  color: $boschBlack;
  background-color: transparent;

  &:first-child {
    @include make-pagination-button-previous($font-size);
    &:hover {
      color: $pagination-hover-color;
      background-color: transparent;
    }
  }

  &:last-child {
    @include make-pagination-button-next($font-size);
    &:hover {
      color: $pagination-hover-color;
      background-color: transparent;
    }
  }
}

//-------------------------------------------------------------------------------------------------------------- ELEMENT STATES
@mixin make-pagination-element-hover {
  color: $pagination-selected-color;
  background-color: $pagination-hover-color;

  * {
    color: $pagination-selected-color;
    background-color: $pagination-hover-color;
  }
}

@mixin make-pagination-element-selected($base-size ) {
  color: $pagination-selected-color;
  background-color: $pagination-selected-bg-color;
  cursor: default;
}

@mixin make-pagination-element-disabled {
  opacity: 0.33;
  cursor: not-allowed;
  color: $font-color;

  * {
    cursor: not-allowed;
    color: $font-color;
  }
}

@mixin make-pagination-element-wide($base-size, $font-size) {
  width: fit-content;
  max-width: fit-content;
  border-radius: 25px;
  padding: 0 $gridSize;
}

@mixin make-pagination-ellipsis {
  color: $boschBlack;
  cursor: default;
  
  &:hover {
    color: $boschBlack;
    background-color: transparent;
  }
}

//-------------------------------------------------------------------------------------------------------------- PAGINATION TYPES
@mixin make-pagination($base-size:$pagination-medium-size-base, $font-size:$pagination-medium-font-size) {

  *, *:hover, *:active, *:focus {
    @include reset-pagination-element-pre-defined-styles;
  }

  > *, > *:hover, > *:active, > *:focus {
    @include make-pagination-element($base-size, $font-size);
  }

  > *:hover {
    @include make-pagination-element-hover;
  }
  
  > *:disabled, *.disabled {
    @include make-pagination-element-disabled;
  }
  
  > *.selected {
    @include make-pagination-element-selected($base-size);
  }

  > *.wide {
    @include make-pagination-element-wide($base-size, $font-size)
  }

  > *.ellipsis {
    @include make-pagination-ellipsis;
  }

  > *.mobile {
    display: none;
  }
}

@mixin make-pagination-small {
  @include make-pagination($pagination-small-size-base, $pagination-small-font-size);
}

@mixin make-pagination-medium {
  @include make-pagination($pagination-medium-size-base, $pagination-medium-font-size);
}

@mixin make-pagination-large {
  @include make-pagination($pagination-large-size-base, $pagination-large-font-size);
}

@mixin make-pagination-mobile($base-size:$pagination-medium-size-base, $font-size:$pagination-medium-font-size) {
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    justify-content: space-between;
    
  *, *:hover, *:active, *:focus {
    @include reset-pagination-element-pre-defined-styles;
  }

  > *, > *:hover, > *:active, > *:focus {
    @include make-pagination-element($base-size, $font-size);
    display: none;
  }
  
  > *:disabled, *.disabled {
    @include make-pagination-element-disabled;
  }

  > *.wide {
    @include make-pagination-element-wide($base-size, $font-size)
  }

  > *.mobile, *:first-child, *:last-child {
    display: inline;
  }
}
