@mixin make-toolbar-item {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  height: 6 * $gridSize;
  width: 6 * $gridSize;
  background-color: $boschWhite;

  >*,
  >::slotted(*) {
    display: block;
    margin: 0;
    color: $boschDarkGrayB50;
    font-size: 3 * $gridSize;
    font-weight: bold;
    text-decoration: none;
    text-align: center;
    height: 3 * $gridSize;
    width: 3 * $gridSize;
    min-width: 3 * $gridSize;
    padding: 0;
    background-color: $boschWhite;
  }
}

@mixin make-toolbar-item-hover {
  background-color: $boschLightGray05;
  color: $boschBlack;
}

@mixin make-toolbar-item-active {
  transition: 0s;
  background-color: $boschLightGray05;
}

@mixin make-toolbar-spacing {
  height: 4 * $gridSize;
  border-left: 1px solid $boschLightGrayW25;
  margin: 8px;
}

@mixin make-toolbar() {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  justify-content: center;
  background-color: $boschWhite;
  height: 6 * $gridSize;
  min-width: fit-content;
  max-width: fit-content;

  >.toolbar-spacing,
  >::slotted(.toolbar-spacing) {
    @include make-toolbar-spacing();
  }

  >.toolbar-item,
  >::slotted(.toolbar-item) {
    @include make-toolbar-item();
  }

  >.toolbar-item:hover,
  >::slotted(.toolbar-item:hover) {
    @include make-toolbar-item-hover();
  }

  >.toolbar-item:active,
  >::slotted(.toolbar-item:active) {
    @include make-toolbar-item-active();
  }
}
