@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';

mat-list.mat-list, mat-selection-list.mat-selection-list {
  border: none;
  padding: 0;

  *[md-subheader] {
    margin: 0;
    line-height: 48px;
    padding: 0 16px;
    background-color: $boschLightGrayW75;
    @include make-font-bold($font-size-h6);
  }

  mat-list-item.mat-list-item, mat-list-option.mat-list-item {
    font-size: $font-size-base;
    line-height: initial;
    height: auto;
    min-height: 48px;
    box-sizing: border-box;
    @include make-border(0 0 1px 0, $boschLightGrayW50);

    &:focus {
      outline: none;
    }
    .mat-list-item-content {
      min-height: 48px;
      font-size: $font-size-base;
      padding-top: 8px;
      padding-bottom: 8px;
    }

    .mat-list-text {
      justify-content: center;
      line-height: initial;
    }
  }

  &[dense] {
    mat-list-item.mat-list-item, mat-list-option.mat-list-item {
      height: auto;
      min-height: 40px;

      .mat-list-item-content {
        min-height: 40px;
        padding: 8px 16px;

        .mat-line {
          font-size: inherit;
        }
      }
    }
  }
}

