@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/typography';

snack-bar-container.mat-snack-bar-container {
  border-radius: 0;
  padding: (6*$gridSize - $line-height-computed)/2 2*$gridSize;
  @include make-callout-info();

  &.mat-snack-bar-top {
    margin-top: 24px;
  }

  .mat-simple-snackbar {
    @include make-font-bold($font-size-base);
    justify-content: start;

    &:before {
      @include make-callout-icon();
      margin: auto 16px auto 0;
      content: $Bosch-Ic-information;

      /* fix vertical alignment */
      line-height: $font-size-base !important;
    }
  }

  .mat-simple-snackbar-action {
    margin: -8px -16px -8px 64px;

    button {
      @include make-font-bold(14px);
      border: none;
      padding: 0 8px;
      line-height: initial;
    }
  }

  &.error {
    @include make-callout-danger();

    .mat-simple-snackbar:before {
      content: $Bosch-Ic-notification-error;
    }
  }

  &.success {
    @include make-callout-success();

    .mat-simple-snackbar:before {
      content: $Bosch-Ic-notification-success;
    }
  }

  &.warning {
    @include make-callout-warn();

    .mat-simple-snackbar:before {
      content: $Bosch-Ic-notification-warning;
    }
  }

}
