@import '~@bci-web-core/common-styles/sass/bci/bci-variables';
@import '~@bci-web-core/common-styles/sass/bci/mixins/borders';
@import '~@bci-web-core/common-styles/sass/bci/mixins/icons';
@import '~@bci-web-core/common-styles/sass/bci/bosch-ic';

@mixin paginator-navigation-button {
  min-width: 30px;
  position: relative;
  top: 3px;
  .mat-button-wrapper {
    height: 30px;
    min-width: 30px;
    position: relative;
    top: 5px;

    .mat-paginator-icon {
      display: none;
    }
  }
  .mat-button-ripple.mat-ripple, .mat-button-focus-overlay {
    width: 30px;
    height: 30px;
    top: 5px;
  }

  &:before {
    position: absolute;
    display: block;
    right: 8px;
    top: 0px;
    @content;
  }
}

mat-paginator.mat-paginator {
  color: $font-color;
  font-size: $font-size-base;

  .mat-paginator-container {
    padding: 0;
    margin: $gridSize*2;
    justify-content: space-between;

    .mat-paginator-page-size {
      .mat-paginator-page-size-label {
        margin: 0 $gridSize*2 0 0;
      }

      .mat-paginator-page-size-select {
        min-width: 120px;
        padding: 0;
        margin: 0;

        .mat-form-field-wrapper {
          .mat-form-field-flex {
            top: 0;
          }
        }

        .mat-select-trigger {
          font-size: $font-size-base;

          .mat-select-value {
            padding: 0 0 0 $gridSize*2;
            line-height: $gridSize*6;
          }
        }
      }
    }

    .mat-paginator-navigation-previous {
      @include paginator-navigation-button {
        @include make-icon($bosch-ic-back-left, 16px);
      }
    }

    .mat-paginator-navigation-next {
      @include paginator-navigation-button {
        @include make-icon($bosch-ic-forward-right, 16px);
      }
    }
  }
}
