@import '~@bci-web-core/common-styles/sass/bci/bci-variables';

.mat-menu-panel {
  .mat-menu-content {
    // remove paddings
    &:not(:empty) {
      padding-top: 0;
      padding-bottom: 0;
    }

    // style menu item
    .mat-menu-item {
      padding: 0 $gridSize*2;
      height: $gridSize*5;
      line-height: $gridSize*5;

      // style hover & focused menu item
      &:hover:not([disabled]),
      &.cdk-program-focused:not([disabled]),
      &.cdk-keyboard-focused:not([disabled]),
      &.mat-menu-item-highlighted:not([disabled]) {
        background: $boschDarkBlue;
        color: $boschWhite;
        z-index: 1;

        // set menu item icon color
        .mat-icon {
          color: $boschWhite;
        }
      }

      // sets icon color when disabled
      &[disabled] .mat-icon {
        color: $boschLightGray;
      }

      // style sub menu trigger
      &.mat-menu-item-submenu-trigger {
        // fix position due to different menu item padding
        &::after {
          right: 6px;
        }

        // set white color on highlighted triggered menu item icon
        &.mat-menu-item-highlighted::after {
          color: $boschWhite;
        }
      }

      // set menu item icon margins
      .mat-icon {
        margin: 0 $gridSize*2 0 0;
      }
    }
  }

  // style the menu position indicator
  &.mat-menu-after.mat-menu-below .mat-menu-content:after,
  &.mat-menu-before.mat-menu-below .mat-menu-content:after,
  &.mat-menu-after.mat-menu-above .mat-menu-content:after,
  &.mat-menu-before.mat-menu-above .mat-menu-content:after {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    background: $boschWhite;
    border-right: 1px solid $boschLightGray;
    border-top: 1px solid $boschLightGray;
  }

  // positioning of the menu indicator
  &.mat-menu-below .mat-menu-content:after {
    top: -5px;
    transform: rotate(-45deg);
  }
  &.mat-menu-above .mat-menu-content:after {
    bottom: -5px;
    transform: rotate(135deg);
  }
  &.mat-menu-after .mat-menu-content:after {
    left: 14px;
  }
  &.mat-menu-before .mat-menu-content:after {
    right: 14px;
  }
}
