@mixin make-clickable-element {
  transition: $transition-time-half;
  cursor: pointer;

  &:active,
  &.active {
    color: $boschLightBlue;
  }

  &:active:hover,
  &:hover {
    color: $boschLightBlue50;
  }

  &:disabled, &.disabled {
    color: $boschDarkGray50;
    cursor: not-allowed;

    &:active,
    &.active,
    &:active:hover,
    &:hover {
      color: $boschDarkGray50;
    }
  }
}

@mixin make-button-base($line-height, $font-size, $padding) {
  @include make-font-regular($font-size);
  line-height: $line-height;
  padding: $padding;
  cursor: pointer;
  width: auto;
}

/** Bosch primary button mixins */
@mixin make-primary-button {
  color: $boschWhite;

  @include make-button-base($button-size-base, $font-size-base, 0 2*$gridSize 0);
  @include make-primary-button-background;
  @include make-border(0, transparent);

  &:active,
  &.active,
  &:active:hover,
  &.focus,
  &:focus {
    @include make-primary-button-active;
  }

  &:hover {
    @include make-primary-button-hover;
  }

  &:disabled,
  &:hover:disabled,
  &.disabled {
    @include make-primary-button-disabled;
  }
}

@mixin make-primary-button-large {
  @include make-button-base($button-size-large, $font-size-large, 0 3*$gridSize 0 3*$gridSize);
}

@mixin make-primary-button-small {
  @include make-button-base($button-size-small, $font-size-small, 0 1.5*$gridSize 0 1.5*$gridSize);
}

@mixin make-primary-button-xsmall {
  @include make-button-base($button-size-xsmall, $font-size-tiny, 0 1.5*$gridSize 0 1.5*$gridSize);
}

@mixin make-primary-button-background {
  background-color: $boschDarkBlue;
  background-size: 100% 200%;
}

@mixin make-primary-button-active {
  border-color: $boschLightBlue;
  background-color: $boschLightBlue;
  color: $boschWhite;
  outline: 0;
}

@mixin make-primary-button-hover {
  border-color: $boschLightBlueW50;
  background-color: $boschLightBlueW50;
  color: $boschWhite;
  outline: 0;
}

@mixin make-primary-button-disabled {
  background-color: $boschLightGray;
  border-color: $boschDarkGray50;
  background-image: none;
  cursor: not-allowed;
}

/** Bosch secondary button mixins */
@mixin make-secondary-button {
  color: $boschDarkBlue;
  background-color: transparent;

  @include make-button-base($button-size-base - 4px, $font-size-base, 0 2*$gridSize 0 12px);
  @include make-border(2px, $boschDarkBlue);

  &:hover {
    @include make-secondary-button-hover;
  }

  &:active,
  &.active,
  &:focus {
    @include make-secondary-button-active;
  }

  &:disabled,
  &:hover:disabled,
  &.disabled {
    @include make-secondary-button-disabled;
  }
}

@mixin make-secondary-button-large {
  @include make-button-base($button-size-large - 4px, $font-size-large, 0 3*$gridSize 0 3*$gridSize);
}

@mixin make-secondary-button-small {
  @include make-button-base($button-size-small - 4px, $font-size-small, 0 1.5*$gridSize 0 1.5*$gridSize);
}

@mixin make-secondary-button-xsmall {
  @include make-button-base($button-size-xsmall - 4px, $font-size-tiny, 0 1.5*$gridSize 0 1.5*$gridSize);
}

@mixin make-secondary-button-active {
  border-color: $boschLightBlue;
  color: $boschLightBlue;
  outline: 0;
}

@mixin make-secondary-button-hover {
  border-color: $boschLightBlueW50;
  outline: 0;
  color: $boschLightBlueW50;
}

@mixin make-secondary-button-disabled {
  color: $boschLightGray;
  border-color: $boschLightGray;
  cursor: not-allowed;
}

/** Bosch link button */
@mixin make-link-button {
  color: $boschDarkBlue;
  background-color: transparent;
  border: none;
  @include box-shadow(none);
  @include make-button-base($button-size-base, $font-size-base, 0 2*$gridSize 0 2*$gridSize);

  &::after {
    position: absolute;
    @include make-icon($Bosch-Ic-arrow-expand-right, 18px);
    // @include transition(all 0.4s ease 0s);
  }

  &:hover,
  &:focus {
    @include make-link-button-hover;
  }

  &:active {
    @include make-link-button-active;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] {
    @include make-link-button-disabled;
  }
}

@mixin make-link-button-large {
  @include make-button-base($button-size-large, 20px, 0 3*$gridSize 0 3*$gridSize);

  &::after {
    font-size: 22px;
  }
}

@mixin make-link-button-small {
  @include make-button-base(40px, 14px, 0 1.5*$gridSize 0 1.5*$gridSize);

  &::after {
    font-size: 14px;
  }
}

@mixin make-link-button-hover {
  color: $boschLightBlueW50;
  text-decoration: none;
  outline: 0;

  &::after {
    @include translate(4px, 0);
  }
}

@mixin make-link-button-active {
  color: $boschLightBlue;
}

@mixin make-link-button-disabled {
  color: $boschLightGray;
  text-decoration: none;
  cursor: not-allowed;

  @include transition(none);

  &::after {
    @include translate(0, 0);
  }
}

@mixin make-link-button-no-arrow {
  &::after {
    content: "";
  }
}

/** Block Buttons */
@mixin make-block-button {
  display: block;
  width: 100%;
}
